@import '_base.sass';

//Mobile up to 1000px
@media only screen and (max-width: 1000px) {
    header {
        //@include theme($theme: $color-theme-Darkgreen)
        background-color: $color-theme-Darkgreen;
        color: $color-Darkgreen-font;
        //display: flex
        //flex-flow: columns
        height: 10vh;
        //grid-template-columns: 80vw 20vw
        //grid-template-rows: 10vh 20vh
        //grid-template-areas: "emptySpace1 hamburger" "Heading emptySpace2"
        padding: 2vh 3vw 2vh 3vw;
        nav {
            background: $color-theme-Darkgreen;
            ul {
                margin: auto;
                list-style-type: none;
                padding-top: 10vh;
                li {
                    margin: 1vh 0 1vh 0;
                    font: $font-size-normal $font-heading-backup; }
                a {
                    text-decoration: none; }
                a:link {
                    color: $color-Darkgreen-font; }
                a:visited {
                    color: $color-Darkgreen-font; }
                a:before {
                    content: '';
                    position: absolute;
                    width: 100%;
                    height: 4px;
                    border-radius: 4px;
                    background-color: $color-Darkgreen-purpel;
                    bottom: 0;
                    left: 0;
                    transform-origin: right;
                    transform: scaleX(0);
                    transition: transform .1s ease-in-out; }
                a:active::before {
                    transform-origin: left;
                    transform: scaleX(1); } }

            img {
                width: 7vw;
                height: 8vh;
                margin: auto; } }
        .menuItem {
            display: block;
            padding: 2vh 0 2vh 0; }
        .hamburger {
            background-color: $color-theme-Darkgreen;
            position: fixed;
            z-index: 5000;
            border: none;
            right: 0;
            margin-right: 10vw;
            cursor: pointer; }
        .closeOpt {
            display: none; }
        .globalNav {
            background-color: $color-theme-Darkgreen;
            display: flex;
            flex-flow: column;
            align-items: center;
            position: fixed;
            transform: translateY(-100%);
            transition: transform 0.2s;
            top: 0;
            left: 0;
            rigth: 0;
            bottom: 0;
            width: 100vw;
            z-index: 4999; }
        //JS class
        .menuVisability {
            transform: translateY(0); } } }
/*Desctop */


